import React, { useState } from "react";
import defaultAvatar from "./avatar.svg"; // ✅ imported fallback SVG (Gatsby-safe)

interface AvatarProps {
  url?: string;
  width?: number;
  height?: number;
  alt?: string;
  className?: string;
}

const Avatar: React.FC<AvatarProps> = ({
  url,
  width = 100,
  height = 100,
  alt = "User Avatar",
  className = "",
}) => {
  const [imgLoaded, setImgLoaded] = useState(false);
  const [imgError, setImgError] = useState(false);

  const showFallback =
    !url || imgError || url.trim() === "" || !imgLoaded;

  return (
    <img
      src={showFallback ? defaultAvatar : url}
      alt={alt}
      width={width}
      height={height}
      className={`rounded-full object-cover ${className} ${
        showFallback ? "fallback-avatar" : ""
      }`}
      onLoad={() => setImgLoaded(true)}
      onError={() => setImgError(true)}
      loading="lazy"
    />
  );
};

export default Avatar;
