import React, { useState, useEffect } from "react";
import { Link } from "gatsby";
import { FiX } from "react-icons/fi";

import "./header.css";
import { getContent } from "../../content/content";
import { useLanguage } from "../../context/LanguageContext";
import LanguageToggle from "../LanguageToggle";
import { useIsLoggedIn } from "../../hooks/useIsLoggedIn";
import Avatar from "../shared/Avatar/Avatar";

const Header: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const { lang } = useLanguage();
  const headerContent = getContent(lang).pages.globals.section.headerContent;
  const { isLoggedIn, loading } = useIsLoggedIn();

  const [avatarUrl, setAvatarUrl] = useState<string>("");

  useEffect(() => {
    const handleScroll = () => setScrolled(window.scrollY > 10);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  useEffect(() => {
    // Load stored avatar from localStorage or session
    try {
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}");
      if (storedUser.avatar) setAvatarUrl(storedUser.avatar);
    } catch {
      setAvatarUrl("");
    }
  }, [isLoggedIn]);

  const LogoIcon = headerContent.logo.icon;
  const CtaIcon = headerContent.cta.icon;

  const resolveMobilePath = (path: string) =>
    (headerContent as any).mobileNavOverride[path] || path;

  return (
    <header className={`site-header ${scrolled ? "scrolled" : ""}`}>
      <div className="container">
        {/* --- Logo --- */}
        <div className="logo">
          <LogoIcon className="logo-icon" />
          <Link to="/" className="logo-text">
            <span className="logo-main">{headerContent.logo.mainText}</span>
            <span className="logo-sub">{headerContent.logo.subText}</span>
          </Link>
        </div>

        {/* --- Desktop Nav --- */}
        <nav className="nav desktop-nav">
          <ul>
            {headerContent.navLinks.map((link: any) => (
              <li key={link.text}>
                <Link to={link.path} className="nav-link">
                  {link.text}
                </Link>
              </li>
            ))}
            <li>
              <LanguageToggle />
            </li>

            {/* --- CTA / Avatar --- */}
            <li>
              {loading ? (
                <div style={{ width: 36, height: 36, borderRadius: "50%", background: "#ccc" }} />
              ) : isLoggedIn ? (
                <Link to="/members/dashboard" className="avatar-link">
                  <Avatar
                    url={avatarUrl}
                    width={36}
                    height={36}
                    className="header-avatar"
                  />
                </Link>
              ) : (
                <Link to={headerContent.cta.path} className="cta-btn">
                  <CtaIcon className="btn-icon" />
                  {headerContent.cta.text}
                </Link>
              )}
            </li>
          </ul>
        </nav>

        {/* --- Mobile Menu Toggle --- */}
        <button
          className="menu-toggle"
          onClick={() => setIsOpen(true)}
          aria-label="Open Menu"
        >
          <span className="toggle-bar"></span>
          <span className="toggle-bar"></span>
          <span className="toggle-bar"></span>
        </button>
      </div>

      {/* --- Mobile Menu --- */}
      <div className={`mobile-menu ${isOpen ? "open" : ""}`}>
        <div className="mobile-menu-content">
          <div className="mobile-header">
            <div className="mobile-logo">
              <LogoIcon className="logo-icon" />
              <span>{`${headerContent.logo.mainText} ${headerContent.logo.subText}`}</span>
            </div>
            <button
              className="close-btn"
              onClick={() => setIsOpen(false)}
              aria-label="Close Menu"
            >
              <FiX />
            </button>
          </div>

          <nav className="mobile-nav">
            <ul>
              {headerContent.navLinks.map((link: any) => (
                <li key={link.text}>
                  <Link
                    to={resolveMobilePath(link.path)}
                    onClick={() => setIsOpen(false)}
                    className="mobile-nav-link"
                  >
                    {link.text}
                  </Link>
                </li>
              ))}
              <li style={{ padding: "10px 0" }}>
                <LanguageToggle />
              </li>

              {/* --- Mobile CTA / Avatar --- */}
              <li>
                {loading ? (
                  <div
                    style={{
                      width: 60,
                      height: 60,
                      borderRadius: "50%",
                      background: "#ccc",
                      margin: "10px auto",
                    }}
                  />
                ) : isLoggedIn ? (
                  <Link
                    to="/members/dashboard"
                    onClick={() => setIsOpen(false)}
                    className="mobile-avatar-link"
                    style={{ display: "flex", justifyContent: "center" }}
                  >
                    <Avatar
                      url={avatarUrl}
                      width={40}
                      height={40}
                      className="header-avatar"
                    />
                  </Link>
                ) : (
                  <Link
                    to={headerContent.cta.path}
                    className="mobile-cta-btn"
                    onClick={() => setIsOpen(false)}
                  >
                    <CtaIcon className="btn-icon" />
                    {headerContent.cta.text}
                  </Link>
                )}
              </li>
            </ul>
          </nav>
        </div>
      </div>

      {isOpen && <div className="overlay" onClick={() => setIsOpen(false)} />}
    </header>
  );
};

export default Header;
